'use strict';

export const onScroll = ['$parse', 'utils', ($parse, utils) => {
    return {
        restrict: 'A',
        link: (scope, el, attrs) => {
            const debounce = Number(attrs.debounce);
            const cb = $parse(attrs.onScroll);
            const onScroll = utils.debounce((evt) => {
                cb(scope, {event: evt});
            }, debounce);

            el.on('scroll', onScroll);

            el.on('$destroy', () => {
                el.off('scroll', onScroll);
            });
        }
    };
}];